<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header" ref="header">
        <div class="top">
          <i class="back" @click="back"></i>
          <span class="text">收款二维码</span>
        </div>
        <div class="img-wrapper" ref="imgWrapper">
          <img :src="logoUrl" />
        </div>
        <div class="store-name">
          {{name}}
        </div>
        <div class="record">
          <div class="left">
            <label>应还账单</label>
            <span>{{billMoney | formatMoney}}</span>
          </div>
          <div class="right">
            <label>剩余补贴</label>
            <span>{{subsidyMoney | formatMoney}}</span>
          </div>
        </div>
      </div>
      <div class="content-wrapper" ref="contentWrapper">
        <div class="content" ref="content">
          <div class="qrcode-wrapper">
            <img :src="qrcode" />
          </div>
          <p class="text">我的收款二维码</p>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {getCurrentMerchant,getCurrentUser} from 'api/mine'
  import {ERR_CODE,ORIGIN} from 'api/config'

  export default {
    data () {
      return {
        name: '',
        logoUrl: '',
        qrcode: '',
        billMoney: 0,
        subsidyMoney: 0
      }
    },
    created () {
      this._getCurrentMerchant()
      this._getCurrentUser()
      window.resize = () => {
        this._calcWidth()
      }
    },
    mounted () {
      this._calcWidth()
    },
    methods: {
      back() {
        this.$router.back()
      },
      _calcWidth() {
        let clientWidth = window.innerWidth
        let header = this.$refs.header
        let contentWrapper = this.$refs.contentWrapper
        let content = this.$refs.content
        if(clientWidth < 340) {
          header.style.height = '235px'
          contentWrapper.style.top = '235px'
          content.style.width = '280px'
          content.style.height = '300px'
        } else {
          header.style.height = '270px'
          contentWrapper.style.top = '270px'
          content.style.width = '300px'
          content.style.height = '312px'
        }
      },
       _getCurrentMerchant(){
        getCurrentMerchant().then((res) => {
          if(res.errCode === ERR_CODE){
            this.name = res.data.name
            this.logoUrl = res.data.logoUrl
            this.billMoney = res.data.billMoney
            this.subsidyMoney = res.data.subsidyMoney
          }
        })
      },
      _getCurrentUser(){
        getCurrentUser().then((res) => {
          if(res.errCode === ERR_CODE){
            this.qrcode = ORIGIN + res.data.payQRCoderUrl
          }
        })
      }
    },
    components: {
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    background $color-background
    z-index 1
    .header
      display flex
      align-items center
      flex-direction column
      width 100%
      height 235px
      color #fff
      background #eb4747
      .top
        display flex
        align-items center
        height 44px
        .back
          position absolute
          background url("~common/image/back-white.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
        .text
          color #fff
          font-size $font-size-large
          font-weight bold
      .img-wrapper
        height 70px
        width 70px
        margin-top 11px
        img
          display block
          width 100%
          height 100%
      .store-name
        margin-top 14px
      .record
        display flex
        align-items center
        flex 1
        width 100%
        justify-content space-between
        .left
          margin-left 15px
        .right
          margin-right 15px
        .left,.right
          display flex
          flex-direction column
          label
            font-size $font-size-small-x
          span
            font-size 30px
            margin-top 16px
    .content-wrapper
      position absolute
      left 0
      right 0
      top 235px
      bottom 0
      display flex
      justify-content center
      align-items center
      .content
        background #fff
        width 300px
        height 312px
        border-radius 5px
        .qrcode-wrapper
          width 244px
          height 244px
          margin 14px auto
          img
            display block
            width 100%
            height 100%
        .text
          font-size $font-size-medium-m
          color #4c4c4c
          text-align center


</style>




